import { localStorageGetData, localStorageRemoveData } from "@/util/localStorageData";
import { UserApi } from '@/api/api';
import { removeCookie } from "@/util/cookie";
export default {
  data: function data() {
    return {
      activeIndex: "1",
      activeIndex2: "1",
      welcomeManager: "",
      showUserMsgModal: false
    };
  },
  mounted: function mounted() {
    this.welcomeManager = localStorageGetData("nowLoginUserName");
    this.nowOnlineManager = localStorageGetData("nowLoginUserCount");
  },
  methods: {
    // 退出
    openLogOutModal: function openLogOutModal() {
      var _this = this;

      this.$Modal.confirm({
        title: "退出",
        content: "<p>确定要退出吗</p>",
        onOk: function onOk() {
          _this.$Spin.show();

          UserApi.logOut({
            discount: _this.nowOnlineManager
          }).then(function (res) {
            if (res.data.err == 0) {
              localStorageRemoveData("nowLoginUserCount");
              localStorageRemoveData("nowLoginUserName");
              removeCookie("token");

              _this.$Spin.hide();

              _this.Message('success', res.data.msg);

              _this.$router.replace("/login");
            } else {
              localStorageRemoveData("nowLoginUserCount");
              localStorageRemoveData("nowLoginUserName");
              removeCookie("token");

              _this.$Spin.hide();

              _this.Message('error', res.data.msg);
            }
          }).catch(function (err) {
            localStorageRemoveData("nowLoginUserCount");
            localStorageRemoveData("nowLoginUserName");
            removeCookie("token");

            _this.$Spin.hide();

            _this.Message('error', err.data.msg);
          }); // 禁用后退

          history.pushState(null, null, document.URL);
          window.addEventListener("popstate", function (e) {
            history.pushState(null, null, document.URL);
          }, false);
        },
        onCancel: function onCancel() {}
      });
    },
    // 显示当前用户信息
    showNowUser: function showNowUser() {
      this.showUserMsgModal = true;
    },
    closeShowUser: function closeShowUser() {
      this.showUserMsgModal = false;
    },
    // 封装消息提示
    Message: function Message(type, content, duration, closable) {
      var msDuration, msClosable;

      if (duration === null || duration === undefined || duration === '') {
        msDuration = 1.5;
      } else {
        msDuration = duration;
      }

      if (closable === null || closable === undefined || closable === '') {
        msClosable = false;
      } else {
        msClosable = closable;
      }

      this.$Message.destroy();
      this.$Message[type]({
        content: content,
        duration: msDuration,
        closable: msClosable
      });
    }
  }
};